import { Component, OnInit, Input } from '@angular/core';
import { Sessao } from 'src/app/models/sessao';
import { SessaoService } from 'src/app/services/sessao.service';
import { ModalController, ToastController } from '@ionic/angular';
import { PacienteService } from 'src/app/services/paciente.service';
import { Paciente } from 'src/app/models/paciente';

@Component({
  selector: 'app-paciente-responder-sessao',
  templateUrl: './paciente-responder-sessao.component.html',
  styleUrls: ['./paciente-responder-sessao.component.scss'],
})
/**
 * Modal do paciente para responder sobre a sessão ocorrida
 */
export class PacienteResponderSessaoComponent implements OnInit {

  @Input()
  sessao: Sessao;

  //Comentário do paciente a respeito da sessão
  comentario:string;

  //Paciente logado
  paciente: Paciente = null;

  constructor(private sessaoService:SessaoService, private modalController:ModalController, private pacienteService:PacienteService, public toastController: ToastController) { }

  async ngOnInit() {
    this.paciente = await this.pacienteService.getPacienteLogado();
  }

  /**
   * Salva os dados do paciente em relação a sessão 
   */
  async salvar() {
    let toast;
    if (this.comentario == null)
      toast = await this.toastController.create({message:'Informe o que achou da sessão', duration: 2000})
    else {
      this.sessaoService.responder(this.sessao.id, this.comentario);

      toast = await this.toastController.create({message:'Opinião salva', duration: 2000})

      this.modalController.dismiss({
        'dismissed': true
      });
    }
    toast.present();
    
  }
}
