import { Component, OnInit, Input } from '@angular/core';
import { ModalController, ToastController, NavParams } from '@ionic/angular';
import { QuestionarioService } from 'src/app/services/questionario.service';
import { Questionario } from 'src/app/models/questionario';

@Component({
  selector: 'app-paciente-responder-dia',
  templateUrl: './paciente-responder-dia.component.html',
  styleUrls: ['./paciente-responder-dia.component.scss'],
})
/**
 * Modal para o Usuário responder o questionário diario
 */
export class PacienteResponderDiaComponent implements OnInit {

  //Lista dos dias
  @Input() dias:string[];
  //Dia atual da lista  
  indiceDias:number = 0;
  
  abaSelecionada = 1;
  questionario: Questionario;

  //Dicas
  dicas:{img:string, dica:string}[] = [
    {dica: 'Formigamento ou dormencia nos braços e pernas pode ser comum durante a abstinencia, isso ocorre por melhoramento na sua circulação sanguínea.', img:'/nao-fume-site/assets/imgs/dicas/dormente.png'},
    {dica: 'Poderá ser comum sentir-se tonto nos primeiros dias. Isso ocorre porque seu cerebro está recebendo mais oxigêncio. Esses sintomas devem desaprecer com o tempo.', img:'/nao-fume-site/assets/imgs/dicas/tonto.jpg'},
    {dica: 'Será comum nos primeiros dias tossir mais do que quando fumava. Isso ocorre devido ao fato dos cílios estarem limpado os brônquios durante o dia.', img:'/nao-fume-site/assets/imgs/dicas/tosse.jpg'},
    {dica: 'Beba muitos copos de água durante o dia. Isso irá ajudar a diminuir o desejo pelo cigarro.', img:'/nao-fume-site/assets/imgs/dicas/agua.jpg'},
    {dica: 'Sempre carregue um lanche. Ter algo para mastigar, alivia a vontade de fumar.', img:'/nao-fume-site/assets/imgs/dicas/mastigar.jpg'},
    {dica: 'Fantasie: Para relaxar, sente-se em um local calmo com pernas cruzadas, conte até 10, esqueça o ambiente e pense em apenas deixar seu corpo relaxado.', img:'/nao-fume-site/assets/imgs/dicas/fantasie.jpg'},
    {dica: 'Realize exercícios de respiração profunda. Em pé, com os pés afastados e ligeiramente dobrados, puxe o ar lentamente pelo nariz. Prenda um pouco a respiração e o libere. Repita o processo 4 vezes', img:'/nao-fume-site/assets/imgs/dicas/respire.jpg'},
    {dica: 'Pratique atividades físicas com aumento gradual. Isso ajudará a diminuir o estress, mas lembre-se de procurar um médico antes.', img:'/nao-fume-site/assets/imgs/dicas/exercicio.jpg'},
    {dica: 'Relaxe o corpo e a mente, praticando atividades tranquilas', img:'/nao-fume-site/assets/imgs/dicas/relaxamento.jpg'},
    {dica: 'Seja positivo, use frases como "eu penso...", "eu acredito...", fale com as pessoas olhando nos olhos e com vontade e disposição', img:'/nao-fume-site/assets/imgs/dicas/positivo.jpg'},
  ];

  frasesMedicamento: string[] = [
    'Lembre-se, você não precisa do medicamento para superar o vício. Você é capaz!'
  ]

  aleatorio:number = null;


  constructor(private modalController:ModalController, private toastController: ToastController, 
              private questionarioService: QuestionarioService) { }

  ngOnInit() {
    this.questionario = new Questionario(this.dias[this.indiceDias]);
  }

  /**
  * Salva o questionário 
  */
  async salvar() {

    let toast;
    if (this.questionario.fumou == null)
      toast = await this.toastController.create({message:'Informe se fumou', duration: 2000})
    else if (this.questionario.fumou && this.questionario.cigarros == null)
      toast = await this.toastController.create({message:'Preencha quantia de cigarros', duration: 2000})
    else if (this.questionario.humor == null)
      toast = await this.toastController.create({message:'Selecione o humor', duration: 2000})
    else if (this.questionario.exercitou == null)
      toast = await this.toastController.create({message:'Selecione se tem se exercitado', duration: 2000})
    else if (this.questionario.vontadeFumar == null)
      toast = await this.toastController.create({message:'Selecione a vontade de fumar', duration: 2000})
    else {
      this.questionarioService.responder(this.questionario);
      toast = await this.toastController.create({message:'Questionário salvo', duration: 2000 })
      this.aleatorio = Math.floor(Math.random() * this.dicas.length); //Seleciona uma dica aleatória para o paciente
      this.abaSelecionada = 2;
    }
    toast.present();
  }

  /** Passa para o próximo dia */
  proximo() {

    this.indiceDias++; //Responde um dia
    if (this.indiceDias < this.dias.length) { //Checa se ainda há dias
      this.abaSelecionada = 1; //Aba de perguntas    
      this.questionario = new Questionario(this.dias[this.indiceDias])
    } else {
      this.aleatorio = Math.floor(Math.random() * this.frasesMedicamento.length);
      this.abaSelecionada = 3; //Aba final
    }
  }

  /** Fecha o Modal */
  fechar() {
    //Fecha o próprio modal
    this.modalController.dismiss({
      'dismissed': true
    })
  }
}
