import { Injectable } from '@angular/core';
import * as moment from 'moment'
import { Questionario } from '../models/questionario';
import { Paciente } from '../models/paciente';
import * as firebase from 'firebase';
import { PacienteService } from './paciente.service';
import { Events } from '@ionic/angular';
import { ConquistaService } from './conquista.service';

@Injectable({
  providedIn: 'root'
})
/**
 * Controla os dados dos questionários do paciente
 */
export class QuestionarioService {

  private paciente:Paciente;
  private questionarioDB: firebase.firestore.CollectionReference;
  private pacienteDB: firebase.firestore.CollectionReference;

  constructor(private pacienteService:PacienteService, private conquistaService:ConquistaService) {
    firebase.auth().onAuthStateChanged(user => {
      if (user != null) {
        this.pacienteService.getPacienteLogado().then(p => {
          this.paciente = p; 
          this.pacienteDB = firebase.firestore().collection('pacientes');
          this.questionarioDB = firebase.firestore().collection('pacientes').doc(p.uid).collection('questionariosDiarios');
        });
      }
    }) 
  }

  /**
   * Coleta os dados das respostas do paciente
   * @param questionario Questionario
   */
  public responder(questionario: Questionario) {
    //Adiciona o questionário
    console.log(questionario);
    this.questionarioDB.doc(questionario.dia).set(Object.assign({}, questionario));
    
    //Atualiza os dados do paciente
    let cigarrosEvitados = Math.max(0, this.paciente.mediaCigarros - questionario.cigarros);
    let economizado = cigarrosEvitados * this.paciente.precoCigarro;

    if (questionario.exercitou) this.paciente.diasPraticoExercicio++;
    if (!questionario.fumou) {
      this.paciente.diasSeguidosSemFumar++
      this.paciente.diasSemFumar++
      this.paciente.maxDiasSemFumar = Math.max(this.paciente.diasSeguidosSemFumar, this.paciente.maxDiasSemFumar);
    } else {
      this.paciente.diasSeguidosSemFumar = 0;
    }
    this.paciente.questionariosDiarios.push(questionario);
    this.paciente.cigarrosEvitados += cigarrosEvitados; //Atualiza totoal de cigarros
    this.paciente.dinheiroAcumulado += economizado;     //Atualiza dinheiro acumlado e disponivel
    this.paciente.dinheiroDisponivel += economizado;
    this.paciente.ultimoDiaRespondido = moment(questionario.dia).add(1, 'days').format('YYYY-MM-DD');//Atualiza a data do ultimo questionário respondido
    let paciente = {...this.paciente};
    console.log(paciente)
    delete paciente.questionariosDiarios;
    
    this.pacienteDB.doc(this.paciente.uid).set(paciente);

    this.conquistaService.liberarConquistas(this.paciente);
  }

  /**
   * Checa se tem questionário para responder 
   */ 
  public buscarQuestionariosAbertos(): string[] {
      let ultimoDiaRespondido =  this.paciente.ultimoDiaRespondido;
      let diasResponder:string[] = [];
      let diaAtual = moment().format('YYYY-MM-DD');
      //Recupera os dias não respondidos
      while (ultimoDiaRespondido < diaAtual) {
        diasResponder.push(ultimoDiaRespondido); //Adiciona no inicio do vetor
        ultimoDiaRespondido = moment(ultimoDiaRespondido).add(1, 'days').format('YYYY-MM-DD');
      }
      return diasResponder;
  }

  /**
   * Retorna o histórico dos dias que o paciente fumou ou não
  */
  public async buscarQuestionariosRespondidos() {
    return await this.questionarioDB.get().then(snapshot => {
        let questionarios: Questionario[] = []
        snapshot.forEach(doc => {
            questionarios.push(Object.assign(new Questionario, doc.data()));
        })
        return questionarios;
    })
  }

  
}
